<template>
  <div class="container-fluid mt-3">
    <div class="container">
      <pilihRiwayat @riwayat="handleRiwayat"/>
   <input class="form-control" type="text" :value="'Hadist riwayat : ' + riwayat" aria-label="readonly input example" readonly>
      <div v-for="d in data.data" :key="d.number">
        <cardHadist :data="d"/>
      </div>
    </div>
 </div>
</template>

<script>
//import Search from '../components/Search'
import pilihRiwayat from '../components/pilihRiwayat'
import cardHadist from '../components/cardHadist'

export default {
  name: 'Home',
  data(){
    return {
      path: 'api/data/json/hadith/',
      url: 'https://islamic-api-indonesia.herokuapp.com/',
      riwayat: '',
      value: '',
      furl: '',
      data: []
    }
  },
  components: {
    pilihRiwayat,
//    Search,
    cardHadist
  },
  methods: {
    handleRiwayat(r) {
      this.riwayat = r;
      this.handleFind()
    },
    async handleFind() {
      this.furl = this.url + this.path + this.riwayat;
      try {
        this.data = await this.axios.get(this.furl);
      } catch (e) {
        alert(e)
      }
    }
  }
}
</script>
